/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-remembr-issue {
	display: flex;
	flex-direction: column;
	gap: 0.3em;

	border: 1px dotted rgba(0, 0, 0, 0.5);
	padding: 1em;
	border-radius: 1em;

	.wp-block-remembr-issue--question {
		font-weight: bold;
	}

	.wp-block-remembr-issue--question, .wp-block-remembr-issue--response {
		display: flex;
		align-items: start;
		gap: 0.5em;
	}
}
